<template>
  <div class="candidatos">
    <h1>Candidato:</h1>
    <br>
    <div>Nombre es {{ candidate }}</div>
    <input type="text" v-model="candidate" placeholder="Nombre candidato">
    <br>
    <button class="button is-primary" @click="setCandidate()">Crear Candidato</button>
  </div>
</template>

<script lang="js">
import web3 from "../../contracts/web3"
import instance from "../../contracts/contractInstance"; 
import Election from '../../build/contracts/Election.json'

export default {
  data() {
    return {
      candidate: null,
      name: null,
      currentAddress: null,
      electionContract: Election,
      tableData: [],
      columns: [
        {
          field: "id",
          label: "ID",
          width: "40",
          numeric: true
        },
        {
          field: "name",
          label: "Nombre"
        },
        {
          field: "votes",
          label: "Votos"
        }
      ]
    };
  },

  methods:{
    async setCandidate(){
      web3 = new Web3(web3.currentProvider.selectedAddress)
      this.votanteAddress = web3.currentProvider
      let electionContract = instance
      let addCandidate = await electionContract.methods.addCandidate(this.candidate).send({from: this.votanteAddress})
      //console.log(vote)
    }    
  }
};
</script>

